<template>
  <el-dialog class="add-together-popup" :title="step == 1?'':''" :visible="showDialog" @close="onClose()" width="600px">
    <div v-if="step == 1">
      <el-form  label-position="left" label-width="100px">
      <el-form-item label="">
            <el-input v-model="formItem.itemTitle" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
            <el-select v-model="formItem.groupName" placeholder="">
                <el-option v-for="(member,i) in groups" :label="member"  :value="member" :key="i"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="">
            <el-radio-group v-model="formItem.category" placeholder="">
                <el-radio label=""  value=""></el-radio>
                <el-radio label=""  value=""></el-radio>
            </el-radio-group>
        </el-form-item>
         <el-form-item label="">
            <el-input v-model="formItem.desc" type="textarea" placeholder=""></el-input>
        </el-form-item>
        <!-- <el-upload
            action="https://jsonplaceholder.typicode.com/posts/"
            list-type="picture-card">
        <i class="el-icon-plus"></i>
        </el-upload> -->
        <el-form-item label=""> 
            <el-date-picker
              v-model="formItem.expired_time"
              type="date"
              placeholder="">
            </el-date-picker>
        </el-form-item>
        <el-form-item>
            <template slot="label">
              <p class="label-text">
                <el-tooltip effect="dark" content="Top Center " placement="top">
                <div slot="content"><br/><br/>//</div>
                <span class="instruction">?</span>
              </el-tooltip>
              </p>
              
            </template>
            
            
            <el-radio-group v-model="formItem.rule">
                <el-radio v-for="(category,i) in ruleList" :label="category.type"  :value="category.name" :key="i">
                  {{category.name}}
                </el-radio>
            </el-radio-group>
        </el-form-item>
        </el-form>
        <el-form class="item-detail-container">
        <div class="item-group-name"  v-if="formItem.rule == 'C'||formItem.rule == 'D'"></div>
        <div class="item-detail" v-for="(item,i) in formItem.itemListA" :key="i">
          <el-form-item class="item-detail-item"
          >
            <el-input v-model="item[0]" placeholder=""></el-input>
          </el-form-item>
          <el-form-item class="item-detail-item"
          >
            <el-select v-model="item[1]" placeholder="">
                <el-option v-for="(num,i) in 20" :value="num" :key="i"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item class="item-detail-item"
          >
            <el-input v-model="item[2]" placeholder=""></el-input>
          </el-form-item>
          <el-button @click="removeItem(i,'A')"></el-button>
       </div>
        <el-button @click="addItem('A')"></el-button>
        </el-form>
        <el-form class="item-detail-container"  v-if="formItem.rule == 'C'||formItem.rule == 'D'">
        <div class="item-group-name"></div>
        <div class="item-detail" v-for="(item,i) in formItem.itemListB" :key="i">
          <el-form-item class="item-detail-item"
          >
            <el-input v-model="item[0]" placeholder=""></el-input>
          </el-form-item>
          <el-form-item class="item-detail-item"
          >
            <el-select v-model="item[1]" placeholder="">
                <el-option v-for="(num,i) in 20" :value="num" :key="i"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item class="item-detail-item"
          >
            <el-input v-model="item[2]" placeholder=""></el-input>
          </el-form-item>
          <el-button @click="removeItem(i,'B')"></el-button>
       </div>
        <el-button @click="addItem('B')"></el-button>
        </el-form>
    
    </div>
     <div class="check-info" v-else>
       <p><span class="form-title"></span>{{formItem.itemTitle}}</p>
       <p><span class="form-title"></span>{{formItem.groupName}}</p>
       <p><span class="form-title"></span>{{formItem.desc}}</p>
       <p><span class="form-title"></span>{{formItem.expired_time}}</p>
       <p><span class="form-title"></span></p>
       <div class="item-list">
         <table class="item-list-table">
           <tr>
             <th></th>
             <th></th>
             <th></th>
           </tr>
           <tr v-for="(item,i) in formItem.itemListA" :key="i">
             <td>{{item[0]}}</td>
             <td>{{item[1]}}</td>
             <td>{{item[2]}}</td>
           </tr>
         </table>
       </div>
       <div class="item-list" v-if="formItem.rule == 'C'|| formItem.rule == 'D'">
         <table class="item-list-table">
           <tr>
             <th></th>
             <th></th>
             <th></th>
           </tr>
           <tr v-for="(item,i) in formItem.itemListB" :key="i">
             <td>{{item[0]}}</td>
             <td>{{item[1]}}</td>
             <td>{{item[2]}}</td>
           </tr>
         </table>
       </div>
    </div>
    <div slot="footer" class="dialog-footer">    
              <el-button @click="onClose()">{{step == 1?"":""}}</el-button>
              <el-button type="primary" @click="onSubmit()">{{step == 1?"":""}}</el-button>
          </div>
        </el-dialog>
</template>

<script>
import { mapState } from 'vuex'
import {createTogetherItem} from '../../../api/together'
export default {
   props: {
        showDialog: {
            type: Boolean,
            require: true
        }
    },
    data(){
      return {
        step: 1,
        // '',
          // '',
          // '',
          // ''
        ruleList:[
          {type:'A',name:''},
          {type:'B',name:''},
          {type:'C',name:''},
          {type:'D',name:''},
        ],
        formItem:{
          itemTitle:"",
          groupName:"",
          category:"",
          desc:"",
          expired_time:"",
          rule:"",
          itemListA:[['',null,null]],
          itemListB:[['',null,null]],
        },
      }
    }, 
    computed:{
        ...mapState({
            groups: state => state.basic.groups,
        }),
        
    },
    watch:{
      showDialog(newValue){
        if (newValue){
          this.step = 1
          this.formItem = {
          title:"",
          groupName:"",
          category:"",
          desc:"",
          expiredDate:"",
          rule:"",
          itemListA:[['',null,null]],
          itemListB:[['',null,null]],
        }
        }
      }
    },
    methods: {
      onClose(){
         if (this.step == 1){
          this.$store.commit('user/hideAddTogether')
        } else {
          this.step =1
        }
        },
        removeItem(index,type) {
          if (type =='A'){
            this.formItem.itemListA.splice(index, 1)
          } else {
            this.formItem.itemListB.splice(index, 1)
          }
          
      },
      onSubmit(){
        if (this.step == 1){
          this.step = 2
        } else {
          let params = this.formItem
          params.creator = this.$store.state.user.userName
          createTogetherItem(params).then(res => {
            if (res.status == 'success') {
                    this.$message.success('')
                    this.$store.commit('user/hideAddTogether')
                    if (this.$route.path == '/together'){
                        this.$router.push('/reload')
                    } else {
                        this.$router.push('/together')
                    }
            
                }
            }).catch(err => {
                console.log(err);
            });
        }
            
        },
      addItem(val) {
        if (val == 'A'){
          this.formItem.itemListA.push(['',null,null]);
        } else {
          this.formItem.itemListB.push(['',null,null]);
        }
        
        console.log(this.formItem.itemList)
      }
    }

}
</script>

