import request from '../utils/https'

// 
export function getTogetherItemList (params) {
    return request({
        url: '/together/itemList',
        method: 'post',
        data:params
    })
}

// 
export function createTogetherItem (params) {
    return request({
        url: '/together/createItem',
        method: 'post',
        data:params
    })
}
